@import './reset.css';
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap');

:root {
	/* Colors */
	--blackpure: #000;
	--black: #171718;
	--black-border: #26292d;
	--white: #fff;
	--purple: #5c62ec;

	/* Nav */
	--nav-bg: var(--black);
	--nav-border: var(--black-border);
	--nav-text: var(--white);

	/* Header */
	--header-bg: var(--black);
	--header-text: var(--white);

	/* Footer */
	--footer-bg: var(--black);
	--footer-text: var(--white);

	/* Light mode */
	--page-bg: var(--white);
	--text-color: var(--black);
	--accent: var(--purple);
	--title-1: var(--accent);

	--project-card-bg: var(--white);
	--project-card-text: var(--black);
	--box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}



html,
body {
	
	min-height: 100vh;

	font-family: 'DM Sans', sans-serif;
	letter-spacing: -0.5px;

	/* Light mode */
	background-color: var(--page-bg);
	color: var(--text-color);
}
#root{
	min-height: 100vh;
}
.App{
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}
/* General */

.container {
	min-height: 40px;
	bottom: 0px;
	margin: 15px auto;
	text-align: center;
	bottom: 0px;
	max-width: 1200px;
}




.none {
	display: none !important;
}

.section {
	height: auto;
	bottom: 0px;
}

.title-1 {
	margin-top: 20px;
	margin-bottom: 30px;
	font-size: 60px;
	font-weight: 700;
	line-height: 1.3;
	color: rgb(78, 228, 255);
	text-align: center;
}

.title-2 {
	color:white;
	text-align: center;
	margin-bottom: auto;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.3;
}

/* Nav */

.nav {
	padding: 15px 300px;
	background-color: var(--nav-bg);
	border-bottom: 1px solid var(--nav-border);
	color: var(--nav-text);
	letter-spacing: normal;
}

.nav-row {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	column-gap: 30px;
	row-gap: 20px;
	flex-wrap: wrap;
}

.logo {
	margin-right: auto;
	color: var(--nav-text);
	font-size: 24px;
	font-family: 'Poppins', sans-serif;
}

.logo strong {
	font-weight: 700;
}

.nav-list {
	display: flex;
	flex-wrap: wrap;
	row-gap: 10px;
	align-items: center;
	column-gap: 40px;
	font-size: 16px;
	font-weight: 500;
	font-family: 'Poppins', sans-serif;
}

.nav-list__link {
	color: var(--nav-text);
	transition: opacity 0.2s ease-in;
}

.nav-list__link:hover {
	opacity: 0.8;
}

.nav-list__link--active {
	position: relative;
}

.nav-list__link--active::before {
	content: '';

	position: absolute;
	left: 0;
	top: 100%;

	display: block;
	height: 2px;
	width: 100%;
	background-color: rgb(78, 228, 255);
}

/* Dark mode btn */

.dark-mode-btn {
	order: 9;

	position: relative;
	display: flex;
	justify-content: space-between;

	width: 51px;
	height: 26px;
	padding: 5px;

	border-radius: 50px;
	background-color: #272727;
}

.dark-mode-btn::before {
	content: '';

	position: absolute;
	top: 1px;
	left: 1px;

	display: block;
	width: 24px;
	height: 24px;

	border-radius: 50%;
	background-color: #fff;

	transition: left 0.2s ease-in;
}

.dark-mode-btn--active::before {
	left: 26px;
}

.dark-mode-btn__icon {
	position: relative;
	z-index: 9;
}

/* Header */

.header {
height: 90vh;

	align-items: center;

	background-color: var(--header-bg);
	background-image: url('./../assets/3.jpg');
	background-repeat: no-repeat;
	background-size: 100%;
	background-position: center center;
	
	color: var(--header-text);
	text-align: center;
}

.header__wrapper {
	
	margin-top: 280px;
	margin-bottom: 280px;
	margin-right: 600px;
	
	
}

.header__title {
	color:white;
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4;
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
	font-style: normal;
	color: rgb(78, 228, 255);
}

.header__text {
	margin-bottom: 40px;
	font-size: 18px;
	line-height: 1.333;
	color:white;
}

.header__text p + p {
	margin-top: 0.5em;
}

.expir{

color: white;
margin: 0px auto;
font-size: 16px;
	
}

/* Btn */

.btn {
	display: inline-block;
	height: 48px;
	padding: 12px 28px;

	border-radius: 5px;
	background-color: var(--accent);
	color: var(--white);
	letter-spacing: 0.15px;
	font-size: 16px;
	font-weight: 500;

	transition: opacity 0.2s ease-in;
}

.btn:hover {
	opacity: 0.8;
}

.btn:active {
	position: relative;
	top: 1px;
}

/* Btn outline */

.btn-outline {
	display: flex;
	column-gap: 10px;
	align-items: center;

	height: 48px;
	padding: 12px 20px;

	border-radius: 5px;
	border: 1px solid #000;
	background-color: #fff;
	color: #000;

	transition: opacity 0.2s ease-in;
}

.btn-outline:hover {
	opacity: 0.8;
}

.btn-outline:active {
	position: relative;
	top: 1px;
}

/* Projects  */

.projects {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	column-gap: 30px;
	row-gap: 30px;
}





.project-details {
	margin: 0 auto;
	max-width: 865px;

	display: flex;
	flex-direction: column;
	align-items: center;

	text-align: center;
}

.project-details__cover {
	max-width: 100%;
	margin-bottom: 40px;

	box-shadow: var(--box-shadow);
	border-radius: 10px;
}

.project-details__desc {
	margin-bottom: 30px;

	font-weight: 700;
	font-size: 24px;
	line-height: 1.3;
}

/* Content list */

.content-list {
	margin-left: auto;
	margin-right: auto;
	max-width: 570px;
	display: flex;
	flex-direction: column;
	align-items: center;
	
	text-align: center;
}

.content-list a {
	color: rgb(78, 228, 255);
}

.content-list__item {
	color: white;
	margin: auto;
	margin-top: 60px;
	text-align: center;
	font-size: 18px;
	line-height: 1.5;
}

.content-list__item p + p {
	margin-top: 0.5em;
}

/* Footer */

.footer {
padding-top: 10px;
	position: fixed;
  left: 0;
  bottom: 0px;
  width: 100%;
  height: 100px;
	background-color: black;

	display: flex;
	flex-direction: column;
	align-items: center;
row-gap: 20px;
}


	


.social {
	
	display: flex;
	column-gap: 30px;
	align-items: center;
}

.copyright {
	font-size: 16px;
}

.copyright p + p {
	margin-top: 0.5em;
}

@media (max-width: 620px) {
	/* Header  */

	.header {
		min-height: unset;
	}

	.header__title {
		font-size: 30px;
	}

	.header__title strong {
		font-size: 40px;
	}

	/* Nav */

	.nav-row {
		justify-content: space-between;
	}

	.dark-mode-btn {
		order: 0;
	}

	/* General */

	.section {
		bottom: 0px;

		display: flex;
	}

	

	.title-2 {
		margin-bottom: 10px;
		font-size: 30px;
	}

	/* Projects */

	.project__title {
		font-size: 22px;
	}

	/* Project page */

	.project-details__desc {
		margin-bottom: 20px;
		font-size: 22px;
	}

	/* Content List */

	.content-list {
		row-gap: 20px;
	}

	.content-list__item {
		font-size: 16px;
	}
	/* Social */
}
.projects-list{
	background-image: url('./../assets/3.jpg');
	height: 90vh;
}
.contcts-style{
	background-image: url('./../assets/3.jpg');
	height: 90vh;
}
